//
import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const store = new Vuex.Store({
    state:{
        topics:[
            {name:'all',desc:''},
            {name:'good',desc:''},
            {name:'share',desc:''},
            {name:'ask',desc:''},
            {name:'job',desc:''},
            {name:'dev',desc:''},
        ],
        isShowMoreMenus:false,  //actionsheet
        // moreMenus:      //
        isLogined:false,    //
        unloginedMenus:{
            login:'',
        },
        loginedMenus:{
            logout:'',
            center:'',
            msg:'',
            create:''
        },
        loginname: '', //
        accesstoken: '', //
        authorId: '' //id
    },
    getters:{
        moreMenus(state){   //statestorestate
            if(state.isLogined){
                return state.loginedMenus
            }
            return state.unloginedMenus
        }
    },
    mutations:{
        // actionsheet
        mutationIsShowMoreMenus(state,payload){
            state.isShowMoreMenus=payload;
        },
        // 
        mutationIsLogined(state) {
            state.isLogined = true;
        },
        // 
        mutationLoginname:function(state,payload){
            state.loginname=payload;
        },
        // accesstoken
        mutationAccesstoken:function(state,payload){
            state.accesstoken=payload;
        },
        // id
        mutationAuthorId:function(state,payload){
            state.authorId=payload;
        },
        // 
        mutationLogout:function(state){
            state.isLogined=false;
            state.loginname='';
            state.accesstoken='';
            state.authorId='';

        }
    },
    actions:{}
})

export default store;


